import { getAnalysis } from '@/api/analysis';
import Moment from 'moment';
import CommonCardFrame from '@/components/CommonCardFrame';
var formatMoment = 'YYYY-MM-DD';
var timeGroup = [{
  value: 'all',
  name: '全部'
}, {
  value: {
    start_at: Moment().format(formatMoment),
    end_at: Moment().format(formatMoment)
  },
  name: '今天'
}, {
  value: {
    start_at: Moment().add(-7, 'days').format(formatMoment),
    end_at: Moment().add(-1, 'days').format(formatMoment)
  },
  name: '本周'
}, {
  value: {
    start_at: Moment().add(-30, 'days').format(formatMoment),
    end_at: Moment().add(-1, 'days').format(formatMoment)
  },
  name: '本月'
}, {
  value: {
    start_at: Moment().startOf('quarter').format(formatMoment),
    end_at: Moment().endOf('quarter').format(formatMoment)
  },
  name: '本季度'
}, {
  value: {
    start_at: Moment().startOf('year').format(formatMoment),
    end_at: Moment().endOf('year').format(formatMoment)
  },
  name: '本年'
}, {
  value: 'customize',
  name: '自定义'
}];
export default {
  components: {
    CommonCardFrame: CommonCardFrame
  },
  props: {
    config: {
      type: Object,
      required: true,
      default: function _default() {
        return {
          statusGroup: [],
          model: ''
        };
      }
    }
  },
  data: function data() {
    return {
      timeGroup: timeGroup,
      isCustomize: false,
      form: this.$form.createForm(this, {
        name: 'analysisForm'
      }),
      searchLoading: false
    };
  },
  computed: {
    statusGroup: function statusGroup() {
      return this.config.statusGroup;
    }
  },
  methods: {
    onChange: function onChange(e) {
      console.log(e);
    },
    handleReloadClick: function handleReloadClick() {},
    handleSearchClick: function handleSearchClick() {
      var _this = this;

      this.form.validateFields(function (err, values) {
        if (!err) {
          _this.analysisApi(_this.collectForm());
        }
      });
    },
    analysisApi: function analysisApi(params) {
      var _this2 = this;

      this.searchLoading = true;
      getAnalysis(params).then(function (res) {
        if (res.status) {
          _this2.$emit('success', res.data);
        } else {
          _this2.$message.fail('数据获取失败');
        }

        _this2.searchLoading = false;
      });
    },
    handleCustomizeTimeClick: function handleCustomizeTimeClick() {
      this.isCustomize = true;
    },
    onTimeChange: function onTimeChange(e) {
      // 如果是自定义时间，那就将自定义时间的 form-item 显示
      this.isCustomize = e.target.value === 'customize';
    },
    collectForm: function collectForm() {
      var params = this.form.getFieldsValue();

      if (this.isCustomize) {
        params.time = {
          start_at: params.customize_time[0].format(formatMoment),
          end_at: params.customize_time[1].format(formatMoment)
        };
        delete params.customize_time;
      }

      params.start_at = params.time.start_at;
      params.end_at = params.time.end_at;
      params.model = this.config.model;
      return params;
    }
  }
};